<template>
  <div>
    <router-link to="/">Home</router-link> | 
    <router-link to="/join">Join</router-link> | 
    <router-link to="/dashboard">Dashboard</router-link>
    <router-view />
    <!-- <dashboard @removePlayer="removePlayer" v-if="page === 'dashboard'"/>
    <join-view @playerAdded="updatePlayerList" v-if="page === 'join'"/>
    <landing-view @addPlayer="gotoJoinView" 
                  @startGame="gotoDashboard" 
                  @removePlayer="removePlayer"
                  :players="players" 
                  v-if="page === 'landing'"/> -->
  </div>
</template>

<script>
import {APIService} from '@/APIService';
import {globals} from '@/main.js';
// import Dashboard from '@/views/DashboardView.vue';
// import JoinView from '@/views/JoinView.vue';
// import LandingView from '@/views/LandingView.vue'

const apiService = new APIService();

export default {
  name: 'App',
  components: {
    // Dashboard,
    // JoinView,
    // LandingView
  },
  data() {
    return {
      page: 'landing',
      playersStatus: [],
      info: null
    }
  },
  computed:{
    players(vm) {
      return vm.playersStatus.map(player => {
        return player.name;
      });
    }
  },
  methods: {
    updatePlayerList() {
      // Get the players and bind to player view
      this.getPlayers();
      this.gotoLandingView();
    },
    removePlayer() {
      if(globals.playerName.length > 0){        
        apiService.removePlayer(globals.playerName).then((data) => {
          window.console.log(data);
          globals.playerName = "";
          this.getPlayers();
          this.gotoLandingView();
        });      
      }
      else {
        this.gotoLandingView();
      }
    },
    getPlayers() {
      apiService.getPlayers().then((data) => {
        window.console.log(data);
        this.playersStatus = data.gamePlayers;
      });
    },
    gotoJoinView() {
      this.page = 'join';
    },
    gotoDashboard() {
      this.page = 'dashboard';
    },
    gotoLandingView() {
      this.page = 'landing';
    },
    quitGame() {
      this.removePlayer();
      this.gotoLandingView();
    }
  },
  created(){
    this.getPlayers();
  }
}
</script>

<style scoped>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
